
<template>
  <div class="body">
    <div class="inputBox">
      <van-cell-group>
        <van-field class="CompanyName" v-model="CompanyName" clearable placeholder="请输入企业名称">
          <img height="18px" slot="left-icon" src="/static/image/CompanyName.png">
        </van-field>
        <van-field class="contact" v-model="contact" clearable placeholder="请输入联系人">
          <img height="18px" slot="left-icon" src="/static/image/contact.png">
        </van-field>
        <van-field class="mobelPhone" v-model="mobelPhone" clearable placeholder="请输入手机号">
          <img height="18px" slot="left-icon" src="/static/image/phone.png">
        </van-field>
        <van-field class="address" v-model="address" clearable placeholder="请输入详细地址">
          <img height="18px" slot="left-icon" src="/static/image/address.png">
        </van-field>
        <van-field
          class="password"
          v-model="password"
          :type="close?hiddenPassword:showPassword"
          placeholder="密码（长度在6-16位，请牢记你的密码）"
        >
          <img height="18px" slot="left-icon" src="/static/image/password.png">
          <img
            height="18px"
            slot="right-icon"
            :src="close?eyes.closeEyes:eyes.openEyes"
            @click="changeEyes"
          >
        </van-field>
        <van-field
          class="ConfirmPassword"
          v-model="ConfirmPassword"
          :type="ConfirmClose?hiddenConfirmPassword:showConfirmPassword"
          placeholder="重复密码"
        >
          <img height="18px" slot="left-icon" src="/static/image/ConfirmPassword.png">
          <img
            height="18px"
            slot="right-icon"
            :src="ConfirmClose?ConfirmEyes.closeConfirmEyes:ConfirmEyes.openConfirmEyes"
            @click="changeConfirmEyes"
          >
        </van-field>
        <van-field class="mobelPhone" v-model="mobelPhone" clearable placeholder="附件"></van-field>
      </van-cell-group>
    </div>
    <div class="buttonBox">
      <van-button round type="primary" size="large" class="login">立即申请</van-button>
    </div>
    <div class="other">
      <van-checkbox class="agreement" shape="square" v-model="checked">我同意《服务条款》和《隐私相关政策》</van-checkbox>
    </div>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      active: 0,
      CompanyName: "",
      contact: "",
      password: "",
      ConfirmPassword: "",
      address: "",
      mobelPhone: "",
      SMS: "",
      showConfirmPassword: "number",
      hiddenConfirmPassword: "password",
      showPassword: "number",
      hiddenPassword: "password",
      close: true,
      ConfirmClose: true,
      checked: true,
      code: "获取验证码",
      showConfirmPassword: "number",
      HiddenConfirmPassword: "password",
      icon: {
        normal: "/static/image/eyes.png",
        active: "/static/image/hide.png"
      },
      eyes: {
        openEyes: "/static/image/eyes.png",
        closeEyes: "/static/image/hide.png"
      },
      ConfirmEyes: {
        openConfirmEyes: "/static/image/eyes.png",
        closeConfirmEyes: "/static/image/hide.png"
      }
    };
  },
  mounted() {},
  methods: {
    // 改变眼睛
    changeEyes() {
      this.close = this.close ? false : true;
    },
    // 改变重复密码眼睛
    changeConfirmEyes() {
      this.ConfirmClose = this.ConfirmClose ? false : true;
    },
    // 验证码倒计时
    changeCode() {
      var countdown = 60; //初始值
    },
    settime(val) {
      if (countdown == 0) {
        val.removeAttribute("disabled");
        val.value = "获取验证码";
        countdown = 60;
        return false;
      } else {
        val.setAttribute("disabled", true);
        val.value = "重新发送(" + countdown + ")";
        countdown--;
      }
      setTimeout(function() {
        //设置一个定时器，每秒刷新一次
        settime(val);
      }, 1000);
    }
  }
};
</script>
<style>
.van-cell {
  margin-bottom: 0.4rem;
  border-bottom: 1px solid #a0a0a0;
  width: 8rem;
  padding: 10px 2px;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 5px;
}
.van-field__control {
  font-size: 13px;
  line-height: 0.8rem;
}
.van-field__left-icon img {
  vertical-align: middle;
}
.van-button {
  width: 8rem;
  height: 1rem;
  font-size: 14px;
  line-height: 1rem;
}
.van-cell-group::after {
  border: none;
}
.van-field__label {
  max-width: 1.47rem;
}
.van-checkbox__icon .van-icon {
  width: 0.4rem;
  height: 0.4rem;
  line-height: 0.4rem;
  vertical-align: middle;
}
</style>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang = "less">
.body {
  width: 100%;
  margin-top: 2.4rem;
  text-align: center;
  .inputBox {
    margin-top: 1.01rem;
    img {
      margin-bottom: -0.08rem;
    }
    .VerificationCode {
      padding: 0;
      height: 0.6rem;
      width: 2.5rem;
      border: 1px solid #0055ff;
      background-color: #bfbfbf;
      color: #0055ff;
      font-size: 12px;
      line-height: 0.6rem;
    }
  }
  .buttonBox {
    margin-top: 0.75rem;
    .login {
      background-color: #0055ff;
      /* box-shadow: 0px 1px 1px #0055ff; */
    }
    .register {
      margin-top: 0.34rem;
      box-shadow: 0px 1px 1px #bfbfbf;
    }
  }
  .other {
    width: 7.46rem;
    margin-left: auto;
    margin-right: auto;
    margin-top: 0.45rem;
    font-size: 0.32rem;
  }
  .inputBox .VerificationCode[data-v-0eab33a7] {
    background-color: #fff !important;
  }
  .inputBox[data-v-0eab33a7] {
    margin-top: -1.4rem;
  }
}
</style>



